import React from 'react';
import Slider from 'react-slick';
import './team.css';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

import carousalI2 from "./Images1/close-up-womam3.jpg"

const Caursol1 = () => {
  
    const settings = {
        dots: true,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll:1,
        autoplay: true,
        speed: 1000,
        autoplaySpeed: 2000,
        cssEase: "linear",
        arrows: false,
      };

    return (
        <>
     
        <div style={{display:'flex',paddingLeft:"9%",paddingBottom:"5%",}}>
        <div style={{width:'90%'}} >
           <Slider {...settings}>

            <Maincaursol img ={carousalI2}/>
            <Maincaursol img ={carousalI2}/>
            <Maincaursol img ={carousalI2}/>
            <Maincaursol img ={carousalI2}/>

           </Slider>
        </div>
        </div>
     
        </>
    );
};
const Maincaursol = ({img})=>{
    return(
        <div  style={{width:"100%"}}> 
        <img className="cauI" src={img} alt="carousal_image"/>
      </div>
    );
    //
}

export default Caursol1;