import React from 'react';
import Slider from 'react-slick';
import './team.css';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

import avatar from './Images1/close-up-womam.jpg'
const Team = () => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 2000,
        pauseOnHover: true
    }
    return (
        <div style={{display:'flex',justifyContent:"center"}}>
        <div style={{width:'50%'}} >
        <h1 style={{textAlign:'center',color:'#fca253'}}>Our team</h1>
            <Slider {...settings} className="slider">
              
                <Card/>
                <Card/>  
                <Card/>
                <Card/>                
            </Slider>
        </div>
        </div>
    );
};
const Card = ()=>{
    return (
        <div style={{display:'flex',alignItems:"center",textAlign:"center",flexDirection:"column"}}>
            <img src={avatar} alt="Avatar" class="avatar"/>
             <p>Set a matching height and width that looks good, and use the border-radius property 
             to add rounded corners to an image. 50% will make the image circular:</p>
        </div>
    )
}

export default Team;